import React, { useState } from 'react';
import { 
  Box, 
  Typography, 
  TextField, 
  Button, 
  IconButton, 
  Collapse,
  Select,
  MenuItem,
  FormControl,
  InputLabel
} from '@mui/material';
import ContentCopyIcon from '@mui/icons-material/ContentCopy';
import ExpandMoreIcon from '@mui/icons-material/ExpandMore';
import ExpandLessIcon from '@mui/icons-material/ExpandLess';
import OpenInNewIcon from '@mui/icons-material/OpenInNew';

export const PromptSection = ({
  prompt,
  setPrompt,
  enhancedPrompt,
  handleCopyPrompt,
  systemPrompt,
  setSystemPrompt,
}) => {
  const [showSystemPrompt, setShowSystemPrompt] = useState(false);
  const [selectedAI, setSelectedAI] = useState('deepseek');

  const aiOptions = [
    { value: 'deepseek', label: 'DeepSeek Chat', url: 'https://chat.deepseek.com' },
    { value: 'chatgpt', label: 'ChatGPT', url: 'https://chat.openai.com' },
    { value: 'claude', label: 'Claude', url: 'https://claude.ai' },
    { value: 'gemini', label: 'Gemini', url: 'https://gemini.google.com' },
    { value: 'copilot', label: 'Microsoft Copilot', url: 'https://copilot.microsoft.com' },
    { value: 'perplexity', label: 'Perplexity', url: 'https://www.perplexity.ai' },
  ];

  const handleOpenAI = () => {
    const selectedOption = aiOptions.find(option => option.value === selectedAI);
    if (selectedOption) {
      window.open(selectedOption.url, '_blank', 'noopener,noreferrer');
    }
  };

  return (
    <Box sx={{ display: 'flex', flexDirection: { xs: 'column', md: 'row' }, width: '100%', gap: 2, mt: 2 }}>
      {/* Step 1: Describe your app? */}
      <Box sx={{ flex: 1 }}>
        <Typography variant="h5" component="h2" gutterBottom>
          Step 1: Describe your app?
        </Typography>
        <Box sx={{ position: 'relative', width: '100%' }}>
          <TextField
            label="Enter your prompt"
            variant="outlined"
            value={prompt}
            onChange={(e) => setPrompt(e.target.value)}
            sx={{ width: '100%', mb: 2 }}
            multiline
            rows={4}
          />
        </Box>

        {/* System Prompt Toggle and Textbox */}
        <Box sx={{ position: 'relative', width: '100%', mt: 2 }}>
          <Box sx={{ display: 'flex', alignItems: 'center', gap: 1 }}>
            <Typography variant="h6" component="h3">
              System Prompt (This can be ignored.)
            </Typography>
            <IconButton
              size="small"
              onClick={() => setShowSystemPrompt(!showSystemPrompt)}
              aria-label="toggle system prompt"
            >
              {showSystemPrompt ? <ExpandLessIcon /> : <ExpandMoreIcon />}
            </IconButton>
          </Box>
          <Collapse in={showSystemPrompt}>
            <TextField
              label="System Prompt"
              variant="outlined"
              value={systemPrompt}
              onChange={(e) => setSystemPrompt(e.target.value)}
              sx={{ width: '100%', mt: 2 }}
              multiline
              rows={4}
            />
          </Collapse>
        </Box>
      </Box>

      {/* Step 2: Copy prompt */}
      <Box sx={{ 
        flex: 1, 
        display: 'flex', 
        flexDirection: 'column',
        alignItems: 'center' // This centers the child elements
      }}>
        <Typography variant="h5" component="h2" gutterBottom>
          Step 2: Copy prompt
        </Typography>
        
        {/* Copy Prompt Button - Now centered */}
        <Box sx={{ width: '100%', display: 'flex', justifyContent: 'center' }}>
          <Button
            variant="contained"
            color="primary"
            startIcon={<ContentCopyIcon />}
            onClick={handleCopyPrompt}
            sx={{ width: 'auto', mt: 2, mb: 2 }}
          >
            Copy Prompt
          </Button>
        </Box>

        {/* AI Selector and Open Button - Centered container */}
        <Box sx={{ 
          display: 'flex', 
          flexDirection: 'column',
          alignItems: 'center', 
          width: '100%',
          gap: 2,
          mt: 2
        }}>
          <Typography variant="h6" component="h3">
            Paste to your favourite AI:
          </Typography>
          
          <Box sx={{ 
            display: 'flex', 
            alignItems: 'center', 
            gap: 2,
            justifyContent: 'center',
            width: '100%'
          }}>
            <FormControl sx={{ minWidth: 200 }}>
              <InputLabel id="ai-select-label">Select AI Chat</InputLabel>
              <Select
                labelId="ai-select-label"
                value={selectedAI}
                label="Select AI Chat"
                onChange={(e) => setSelectedAI(e.target.value)}
              >
                {aiOptions.map((option) => (
                  <MenuItem key={option.value} value={option.value}>
                    {option.label}
                  </MenuItem>
                ))}
              </Select>
            </FormControl>
            
            <Button
              variant="outlined"
              color="primary"
              endIcon={<OpenInNewIcon />}
              onClick={handleOpenAI}
              sx={{ height: '56px' }}
            >
              Open
            </Button>
          </Box>
        </Box>
      </Box>
    </Box>
  );
};